@use "./mixins";
/* ===================================== 
    -----main section------
======================================== */
main {
  @include mixins.flexCentering(column, center);
  justify-content: center;
  h2 {
    @include mixins.flexCentering(column, "");
    text-align: center;
    &:after {
      align-self: center;
      display: inline-block;
      margin-top: 1rem;
      height: 1px;
      width: 240px;
      background-color: rgba(0, 0, 0, 0.1);
      text-align: center;
      content: "";
      clear: both;
    }
  }
  /* ===================================== 
    -----about me------
======================================== */
  .about {
    padding: 4rem 1rem;
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    .skillsContainer {
      h3 {
      }
      .skillsList {
        @include mixins.flexCentering(row, center);
        margin-top: 1.4rem;
        flex-wrap: wrap;
        gap: 0.5rem;
        .skill {
          text-transform: uppercase;
          font-size: 0.8rem;
          font-weight: 900;
          background-color: #eee;
          font-family: "Montserrat", sans-serif;
          padding: 0.4rem;
        }
      }
    }
  } /* /about me */
  /* ===================================== 
    -----projects------
======================================== */
  .works {
    background-color: #f1f1f1;
    padding: 0.5rem 1rem;
    width: 100%;
    .worksInfoText {
      @include mixins.flexCentering(column, center);
      padding: 2rem;
    }
    .projects {
      display: flex;
      padding: 0 1rem;
      justify-content: center;
      flex-wrap: wrap;
      gap: 1rem;
      .project {
        @include mixins.flexCentering(column, center);
        background-color: white;
        padding: 2rem;
        margin-bottom: 2rem;
        width: 100%;
        max-width: 30rem;
        p {
          padding: 1rem;
        }
        .project-img {
          margin: 3rem 0;
          width: 100%;
        }
        .projectButtons {
          @include mixins.flexCentering(row, flex-end);
          flex-wrap: wrap;
          gap: 0.5rem;
          margin: auto;
          .button {
            @include mixins.btnStyles();
            margin-top: 1rem;
          }
        }
      }
    }
  } /*/projects */
}
/* ===================================== 
    -----/main section------
======================================== */
