@use "./mixins";
.formSection {
  font-family: "Open Sans Condensed", arial, sans;
  max-width: 50rem;
  padding: 30px;
  margin: 3rem auto;
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.22);

  .formInfo {
    @include mixins.flexCentering(column, center);

    padding: 0 1rem;
    margin-bottom: 2rem;
  }

  form {
    @include mixins.flexCentering(column, center);
    width: 100%;
    input {
      outline: none;
      @include mixins.flexCentering(row, center);
      background: #fff, 10%;
      border: none;
      border-bottom: 1px solid #ccc;
      width: 100%;
      max-width: 40rem;
      padding: 1rem;
      color: rgb(0, 0, 0);
      font-family: Arial, Helvetica, sans-serif;
      font-size: 1rem;
    }
    #message {
      font-size: 1rem;
      margin-left: 0.5rem;
      font-family: Arial, Helvetica, sans-serif;
      outline: none;
      margin-top: 1rem;
      border: none;
      width: 100%;
      max-width: 40rem;
      resize: none;
      height: 10rem;
      border-bottom: 1px solid #ccc;
    }
    button {
      @include mixins.btnStyles();
      margin-top: 1rem;
    }
  }
}
